.translator-compact {
  display: flex;
  align-items: center;
  gap: 8px;
  background: #fff;
  padding: 10px 12px;
  border: 1px solid #ccc;
  border-radius: 8px;
  min-width: min(100%, 280px);
  max-width: 500px;
  margin: 20px auto;
  font-family: sans-serif;
  box-shadow: 0 1px 4px rgba(0, 0, 0, 0.1);
}

.translator-input {
  flex: 1;
  min-width: 250px;
  max-width: 500px;
  padding: 8px 10px;
  font-size: 14px;
  border: 1px solid #ddd;
  border-radius: 6px;
  outline: none;
}

.translator-btn {
  width: 44px;     /* or any fixed size you prefer */
  height: 44px;
  text-align: left;
  padding: 8px 16px;
  background: linear-gradient(135deg, #00c6ff, #0072ff);
  border: none;
  color: white;
  font-weight: bold;
  font-size: 16px;
  border-radius: 16px;
  cursor: pointer;
  transition: transform 0.1s ease, box-shadow 0.2s ease;
  box-shadow: 0 4px 12px rgba(0, 114, 255, 0.3);
}

.translator-btn:hover {
  transform: translateY(-2px);
  box-shadow: 0 6px 18px rgba(0, 114, 255, 0.4);
  background: linear-gradient(135deg, #00b3ff, #005ce6);
}

.translator-btn:disabled {
  background: #ccc;
  box-shadow: none;
  cursor: not-allowed;
  color: #666;
}

.translator-output {
  flex-basis: 100%;
  padding-top: 8px;
  font-size: 14px;
  color: #2e7d32;
}

.translator-select {
  padding: 6px 10px;
  font-size: 14px;
  border-radius: 6px;
  border: 1px solid #ccc;
  background: white;
  cursor: pointer;
  outline: none;
  min-width: 100px;
  transition: border-color 0.3s;
}

.translator-select:focus {
  border-color: #007bff;
}

.translator-history {
  margin-top: 20px;
  background: #f9f9f9;
  border-radius: 8px;
  padding: 12px 16px;
  box-shadow: inset 0 0 5px #ccc;
  max-height: 180px;
  min-width: min(100%, 280px);
  max-width: 500px;
  overflow-y: auto;
}

.translator-history ul {
  padding-left: 16px; /* or 0 if you want no indent */
  margin: 0;
}

.translator-history h3 {
  margin-top: 0;
  margin-bottom: 10px;
  color: #333;
  font-weight: 600;
  font-size: 16px;
}

.translator-history-item {
  margin-bottom: 8px;
  font-family: monospace;
  font-size: 14px;
  color: #444;
  text-align: left;         /* ← force text to align left */
}

.translator-clear-btn {
  background: #ff4d4f;
  color: white;
  border: none;
  padding: 4px 10px;
  border-radius: 6px;
  cursor: pointer;
  font-size: 12px;
  transition: background-color 0.3s;
}

.translator-clear-btn:hover {
  background: #d9363e;
}

.translator-swap-btn {
  background: none;
  border: none;
  cursor: pointer;
  font-size: 20px;
  padding: 0 6px;
  line-height: 1;
  color: #007bff;
  transition: color 0.2s ease;
}

.translator-swap-btn:hover {
  color: #0056b3;
}

.translator-input-highlight {
  color: #0072ff; /* blue tone, or any color you like */
  font-weight: bold;
}

/* Dark mode overrides */
.dark-mode .translator-compact {
  background: #1e1e1e;
  border: 1px solid #444;
  box-shadow: 0 1px 8px rgba(0, 0, 0, 0.8);
  color: #ddd;
}

.dark-mode .translator-input {
  background: #2c2c2c;
  border: 1px solid #555;
  color: #eee;
}

.dark-mode .translator-input::placeholder {
  color: #999;
}

.dark-mode .translator-btn {
  background: linear-gradient(135deg, #0052cc, #003d99);
  box-shadow: 0 4px 12px rgba(0, 52, 153, 0.8);
  color: #fff;
}

.dark-mode .translator-btn:hover {
  background: linear-gradient(135deg, #0041a3, #002d73);
  box-shadow: 0 6px 18px rgba(0, 45, 115, 0.9);
}

.dark-mode .translator-btn:disabled {
  background: #555;
  color: #aaa;
  box-shadow: none;
}

.dark-mode .translator-output {
  color: #81c784; /* lighter green */
}

.dark-mode .translator-select {
  background: #2c2c2c;
  border: 1px solid #555;
  color: #eee;
}

.dark-mode .translator-select:focus {
  border-color: #3399ff;
}

.dark-mode .translator-history {
  background: #121212;
  box-shadow: inset 0 0 5px #222;
  color: #bbb;
}

.dark-mode .translator-history h3 {
  color: #ddd;
}

.dark-mode .translator-history-item {
  color: #ccc;
}

.dark-mode .translator-clear-btn {
  background: #bb4444;
  color: white;
}

.dark-mode .translator-clear-btn:hover {
  background: #992222;
}

.dark-mode .translator-swap-btn {
  color: #3399ff;
}

.dark-mode .translator-swap-btn:hover {
  color: #66b3ff;
}
