.header {
    position: fixed;
    top: 0;
    left: 0;
    width: 100%;
    display: flex;
    justify-content: center;
    align-items: center;
    background-color: rgba(249, 249, 249, 0.5); /* Transparent background */
    z-index: 1000; /* Ensures the header stays on top */
    padding-top: 10px;
    box-shadow: none; /* Remove shadow if unnecessary */
    backdrop-filter: blur(5px);
    flex-shrink: 0;
  }
  
  .icon {
    width: 24px; /* Adjust size as needed */
    height: 24px; /* Adjust size as needed */
    margin: 0 10px; /* Space between icons */
    fill: #000; /* Default color */
    cursor: pointer;
    transition: transform 0.3s ease, fill 0.3s ease;
  }
  
  .icon:hover {
    transform: scale(1.2); /* Enlarge on hover */
  }

  /* Light mode styles */
.light-mode .icon {
  fill: black; /* Default text color for light mode */
}

/* Dark mode styles */
.dark-mode .icon {
  fill: white; /* Text color for dark mode */
}

/* Dark mode overrides */
.dark-mode .header {
  background-color: rgba(34, 34, 34, 0.5); /* Transparent background */
  color: #e0e0e0; /* Light text color */
}