/* Copy style từ LibrarySidebar hoặc dùng chung */
.history-overlay {
    position: fixed; top: 0; left: 0; right: 0; bottom: 0;
    background: rgba(0,0,0,0.5); z-index: 1999;
    opacity: 0; visibility: hidden; transition: all 0.3s;
}
.history-overlay.open { opacity: 1; visibility: visible; }

/* Sidebar bên Trái (cho khác Library bên Phải) hoặc bên Phải tùy bạn */
/* Ở đây tôi để bên Phải đè lên Library cũng được, hoặc chỉnh left:0 nếu muốn bên trái */
.history-sidebar {
    position: fixed; top: 0; right: -320px;
    width: 300px; height: 100%; background: white; z-index: 2000;
    box-shadow: -2px 0 5px rgba(0,0,0,0.2);
    display: flex; flex-direction: column; transition: right 0.3s ease-in-out;
}
.history-sidebar.open { right: 0; }

/* Các class con dùng lại style giống Library cho đồng bộ */
.history-header { padding: 15px; border-bottom: 1px solid #eee; background: #f8f9fa; gap: 10px; display: flex; flex-direction: column; }
.header-top { display: flex; justify-content: space-between; align-items: center; }
.history-content { flex: 1; overflow-y: auto; padding: 10px; }
.clear-all-btn { background: #ff4757; color: white; border: none; padding: 8px; border-radius: 4px; cursor: pointer; width: 100%; }
.clear-all-btn:hover { background: #ff6b81; }
.video-list { list-style: none; padding: 0; margin: 0; }
.video-item { display: flex; gap: 10px; padding: 10px; border-bottom: 1px solid #eee; cursor: pointer; border-radius: 8px; }
.video-item:hover { background: #f0f0f0; }
.video-thumb { width: 80px; height: 60px; object-fit: cover; border-radius: 4px; }
.video-info { flex: 1; display: flex; flex-direction: column; justify-content: center; }
.video-title { font-size: 13px; font-weight: 500; overflow: hidden; display: -webkit-box; -webkit-line-clamp: 2; -webkit-box-orient: vertical; }
.video-cat { font-size: 11px; color: #888; margin-top: 3px; }
.delete-item-btn { background: transparent; border: none; color: #999; font-size: 16px; cursor: pointer; padding: 0 5px; }
.delete-item-btn:hover { color: #ff4757; }

/* Dark mode */
.dark-mode .history-sidebar { background: #222; color: #eee; }
.dark-mode .history-header { background: #333; border-bottom-color: #444; }
.dark-mode .video-item:hover { background: #333; }
.dark-mode .clear-all-btn { background: #c0392b; }