/* src/components/common/QRCodeModal.css */
.qr-overlay {
    position: fixed;
    top: 0; left: 0; right: 0; bottom: 0;
    background-color: rgba(0, 0, 0, 0.7); /* Nền tối mờ */
    display: flex;
    align-items: center;
    justify-content: center;
    z-index: 10000;
    backdrop-filter: blur(5px); /* Hiệu ứng mờ nền phía sau */
    animation: fadeIn 0.2s ease-out;
}

.qr-content {
    background: white;
    padding: 20px;
    border-radius: 16px;
    width: 320px;
    text-align: center;
    box-shadow: 0 10px 25px rgba(0,0,0,0.3);
    position: relative;
    animation: scaleUp 0.3s cubic-bezier(0.175, 0.885, 0.32, 1.275);
}

.qr-header {
    display: flex;
    justify-content: space-between;
    align-items: center;
    margin-bottom: 20px;
}

.qr-header h3 {
    margin: 0;
    font-size: 18px;
    color: #333;
}

.qr-close-btn {
    background: none;
    border: none;
    font-size: 28px;
    cursor: pointer;
    color: #999;
    line-height: 1;
}

.qr-code-wrapper {
    background: white;
    padding: 10px;
    border: 2px dashed #eee;
    border-radius: 8px;
    margin-bottom: 15px;
}

.qr-instruction {
    color: #666;
    font-size: 14px;
    margin-bottom: 5px;
}

.qr-link-preview {
    font-family: monospace;
    background: #f5f5f5;
    padding: 5px;
    border-radius: 4px;
    font-size: 12px;
    color: #555;
}

/* Animations */
@keyframes fadeIn { from { opacity: 0; } to { opacity: 1; } }
@keyframes scaleUp { from { transform: scale(0.8); opacity: 0; } to { transform: scale(1); opacity: 1; } }

/* Dark mode support logic (nếu app có class .dark-mode ở body) */
/* Lưu ý: QR code bắt buộc phải nền trắng, viền đen mới dễ quét, nên phần wrapper vẫn để trắng */
body.dark-mode .qr-content {
    background: #222;
    color: white;
}
body.dark-mode .qr-header h3 { color: white; }
body.dark-mode .qr-instruction { color: #aaa; }
body.dark-mode .qr-link-preview { background: #333; color: #ddd; }