/* Content.css */

/* Container Style */
.container {
    text-align: left;
    margin: 20px auto;
    max-width: 800px;
  }
  .container-main {
    text-align: center;
    margin: 20px auto;
    max-width: 800px;
  }
  /* Heading Styles */
  .heading {
    font-size: 2rem;
    margin: 20px 0;
    color: #007BFF; /* Highlight color */
  }
  
  /* Question and Answer Styles */
  .question {
    font-size: 1.5rem;
    font-weight: bold;
    margin: 10px 0;
    color: #333;
    text-align: left;
    margin-left: 20px;
    margin-right: 20px;
  }
  
  .answer {
    font-size: 1.2rem;
    margin-bottom: 15px;
    line-height: 1.6;
    color: #555;
    text-align: left;
    margin-left: 20px;
    margin-right: 20px;
  }
  
  .paragraph {
    font-size: 1.2rem;
    margin-bottom: 15px;
    line-height: 1.6;
    color: #555;
    text-align: left;
    margin-left: 20px;
    margin-right: 20px;
  }
  
  .highlight {
    font-weight: bold;
  }
  
  /* FAQ Item Styles */
  .faqItem {
    margin: 20px 0;
  }
  
  /* Features Section */
  .featuresContainer {
    display: flex;
    justify-content: space-around;
    align-items: flex-start;
    flex-wrap: wrap;
    gap: 10px;
    margin-top: 20px;
  }
  
  .featureItem {
    display: flex;
    align-items: top;
    max-width: 250px;
    min-height: 500px;
    text-align: left;
    background-color: #f9f9f9;
    padding: 5px;
    border-radius: 8px;
    box-shadow: 0 4px 6px rgba(0, 0, 0, 0.1);
    transition: transform 0.3s, box-shadow 0.3s;
  }
  
  .featureItemHover {
    transform: scale(1.05);
    box-shadow: 0 6px 8px rgba(0, 0, 0, 0.15);
  }
  
  .icon {
    font-size: 2rem;
    color: #007BFF;
    margin-right: 15px;
  }
  
  .featureTitle {
    font-size: 1.3rem;
    font-weight: bold;
    margin-bottom: 10px;
    color: #333;
  }
  
  .dark-mode .heading {
    color: #66b3ff;
  }
  
  .dark-mode .question {
    color: #e0e0e0;
  }
  
  .dark-mode .answer, 
  .dark-mode .paragraph {
    color: #bbb;
  }
  
  .dark-mode .highlight {
    color: #66b3ff;
  }
  
  .dark-mode .featureItem {
    background-color: #444;
    box-shadow: 0 4px 6px rgba(0, 0, 0, 0.3);
  }
  
  .dark-mode .featureItemHover {
    box-shadow: 0 6px 8px rgba(0, 0, 0, 0.4);
  }
  
  .dark-mode .featureTitle {
    color: #e0e0e0;
  }
  
  .dark-mode .icon {
    color: #66b3ff;
  }