/* Buttons Container */
.button-container {
  width: 100%;
  display: flex;
  justify-content: center;
  gap: 20px; /* Space between buttons */
  padding: 10px;
}

/* General Button Styles */
.button-custom {
  width: 44px; /* Slightly larger for better usability */
  height: 44px;
  padding: 12px;
  border-radius: 16px; /* Smoother rounded corners */
  font-size: 1.4rem; /* Adjust font size for better visibility */
  font-weight: bold;
  cursor: pointer;
  transition: background-color 0.3s ease, transform 0.2s ease, box-shadow 0.3s ease;
  border: none;
  color: white;
  display: flex;
  align-items: center;
  justify-content: center;
  box-shadow: 0 4px 8px rgba(0, 0, 0, 0.1); /* Softer shadow for a more modern feel */
}

/* Disabled state for buttons */
.button-custom:disabled {
  background-color: #d3d3d3; /* Lighter grey */
  cursor: not-allowed;
}

/* Hover state for buttons */
.button-custom:hover:not(:disabled) {
  transform: translateY(-3px); /* Smoother hover effect */
}

/* Active state for buttons */
.button-custom:active:not(:disabled) {
  transform: translateY(1px); /* Slight depression on click */
}

/* Specific Button Styles */

/* Previous Button (soothing green, commonly associated with previous actions) */
.previous-button {
  background-color: #4CAF50; /* Green */
}

.previous-button:hover:not(:disabled) {
  background-color: #45a049; /* Slightly darker on hover */
}

/* Like Button (energetic pink, evokes positivity) */
.like-button {
  background-color: #FF69B4; /* Hot Pink */
}

.like-button:hover:not(:disabled) {
  background-color: #e55b9e; /* Darker pink for hover */
}

/* Dislike Button (neutral grey with a hint of blue) */
.dislike-button {
  background-color: #606060; /* Dark Grey */
}

.dislike-button:hover:not(:disabled) {
  background-color: #505050; /* Darker grey on hover */
}

/* Next Button (calming blue, often associated with "next" or "forward" actions) */
.next-button {
  background-color: #1E90FF; /* Dodger Blue */
}

.next-button:hover:not(:disabled) {
  background-color: #1c7ed6; /* Slightly darker blue on hover */
}

