/* --- ZEN MODE STYLES --- */
.zen-mode {
    position: fixed !important;
    top: 0;
    left: 0;
    width: 100vw;
    height: 100vh;
    z-index: 9999; /* Đè lên tất cả */
    background-color: #000; /* Nền đen tuyệt đối */
    padding: 0 !important;
    margin: 0 !important;
    display: flex;
    flex-direction: column;
    justify-content: center;
}

/* Ẩn thanh cuộn khi Zen Mode */
.zen-mode-active body {
    overflow: hidden;
}

/* Control Bar trong Zen Mode: Mờ đi, chỉ hiện khi di chuột */
.zen-mode .button-container {
    position: absolute;
    bottom: 30px;
    left: 50%;
    transform: translateX(-50%);
    opacity: 0; /* Mặc định ẩn hoàn toàn cho tập trung */
    transition: opacity 0.4s ease-in-out;
    background: rgba(255, 255, 255, 0.1);
    padding: 10px 20px;
    border-radius: 30px;
}

.zen-mode .button-container:hover {
    opacity: 1; /* Hiện lên khi rê chuột xuống dưới */
    background: rgba(0, 0, 0, 0.6);
}

/* Các thành phần khác sẽ bị ẩn bởi React Conditional Rendering, 
   nhưng thêm CSS này để chắc chắn ẩn các phần tử global nếu React ko kiểm soát được */
.zen-mode .top-layout,
.zen-mode .ad,
.zen-mode .footer {
    display: none !important;
}

/* --- ZEN MODE TIMER --- */
.zen-timer {
    position: fixed;
    top: 20px;
    right: 30px;
    z-index: 10000; /* Cao hơn cả Zen Mode layer */
    background: rgba(255, 255, 255, 0.1);
    color: rgba(255, 255, 255, 0.7);
    padding: 8px 15px;
    border-radius: 20px;
    font-size: 14px;
    font-weight: 500;
    cursor: pointer;
    border: 1px solid rgba(255, 255, 255, 0.1);
    transition: all 0.3s ease;
}

.zen-timer:hover {
    background: rgba(255, 255, 255, 0.2);
    color: white;
    border-color: rgba(255, 255, 255, 0.3);
}