/* Overlay làm tối nền */
.library-overlay {
    position: fixed;
    top: 0; left: 0; right: 0; bottom: 0;
    background: rgba(0,0,0,0.5);
    z-index: 1999;
    opacity: 0;
    visibility: hidden;
    transition: opacity 0.3s, visibility 0.3s;
}
.library-overlay.open {
    opacity: 1;
    visibility: visible;
}

/* Sidebar trượt từ phải sang */
.library-sidebar {
    position: fixed;
    top: 0; right: -320px; /* Ẩn sang bên phải */
    width: 300px;
    height: 100%;
    background: white;
    z-index: 2000;
    box-shadow: -2px 0 5px rgba(0,0,0,0.2);
    display: flex;
    flex-direction: column;
    transition: right 0.3s ease-in-out;
}
.library-sidebar.open {
    right: 0; /* Hiện ra */
}

.library-header {
    padding: 15px;
    border-bottom: 1px solid #eee;
    background-color: #f8f9fa;
    display: flex;
    flex-direction: column; /* Đổi thành cột để chứa nút Clear All */
    gap: 10px;
}

.header-top {
    display: flex;
    justify-content: space-between;
    align-items: center;
}

/* Nút Clear All */
.clear-all-btn {
    background-color: #ff4757;
    color: white;
    border: none;
    padding: 8px 12px;
    border-radius: 4px;
    cursor: pointer;
    font-size: 14px;
    transition: background 0.2s;
    width: 100%;
}
.clear-all-btn:hover {
    background-color: #ff6b81;
}

/* Thông tin Category nhỏ bên dưới tiêu đề */
.video-cat {
    display: block;
    font-size: 12px;
    color: #888;
    margin-top: 2px;
}

/* Nút xóa từng item (dấu X) */
.delete-item-btn {
    background: transparent;
    border: none;
    color: #999;
    font-size: 18px;
    font-weight: bold;
    cursor: pointer;
    padding: 5px 10px;
    margin-left: 5px;
    border-radius: 50%;
    transition: all 0.2s;
}
.delete-item-btn:hover {
    background-color: #ffecf0;
    color: #ff4757;
}

.close-btn {
    background: none; border: none; font-size: 24px; cursor: pointer; color: #555;
}

/* Content List */
.library-content {
    flex: 1;
    overflow-y: auto;
    padding: 10px;
}
.video-list { list-style: none; padding: 0; margin: 0; }

.video-item {
    display: flex;
    gap: 10px;
    padding: 10px;
    border-bottom: 1px solid #eee;
    cursor: pointer;
    transition: background 0.2s;
    border-radius: 8px;
}
.video-item:hover { background: #f0f0f0; }

.video-thumb {
    width: 80px; height: 60px; object-fit: cover; border-radius: 4px;
}
.video-info { flex: 1; display: flex; align-items: center;}
.video-title {
    font-size: 14px;
    font-weight: 500;
    display: -webkit-box;
    -webkit-line-clamp: 2;
    -webkit-box-orient: vertical;
    overflow: hidden;
    color: #333;
}
.empty-msg { text-align: center; margin-top: 50px; color: #888; }

/* Dark mode support */
.dark-mode .clear-all-btn { background-color: #c0392b; }
.dark-mode .clear-all-btn:hover { background-color: #e74c3c; }
.dark-mode .library-sidebar { background: #222; color: #eee; }
.dark-mode .library-header { background: #333; border-bottom: 1px solid #444; }
.dark-mode .video-title { color: #eee; }
.dark-mode .video-item:hover { background: #333; }
.dark-mode .close-btn { color: #eee; }
.dark-mode .video-cat { color: #aaa; }
.dark-mode .delete-item-btn:hover { background-color: #442225; }