.footer {
  position: relative;
  width: 100%; /* Ensures footer spans the full width */
  box-sizing: border-box; /* Includes padding in width calculation */
  display: flex;
  flex-direction: column;
  justify-content: center;
  align-items: center;
  background-color: #f9f9f9;
  color: #000;
  font-size: 14px;
  padding: 20px 10px;
  box-shadow: 0 -1px 5px rgba(0, 0, 0, 0.1);
  flex-shrink: 0;
  overflow: hidden; /* Prevent content overflow */
  transition: background-color 0.3s, color 0.3s; /* Smooth transition for dark mode */
}

.footer-content {
  display: flex;
  align-items: center;
  text-align: center; /* Center-align text for better responsiveness */
  flex-wrap: wrap; /* Allow wrapping to prevent overflow */
}

.footer-links {
  display: flex;
  flex-wrap: wrap; /* Wrap links to the next line if necessary */
  justify-content: center;
  gap: 15px;
  text-align: center;
  max-width: 100%; /* Avoid overflowing links */
  overflow: hidden;
}

.footer-link {
  text-decoration: none;
  color: #0050AD;
  font-weight: 500;
  transition: color 0.3s;
  white-space: nowrap; /* Prevent links from breaking mid-word */
}

.footer-link:hover {
  color: #003C80;
}

.heart {
  color: red;
  margin-left: 5px;
}

/* Dark mode overrides */
.dark-mode .footer {
  background-color: rgba(34, 34, 34, 0.5); /* Transparent background */
  color: #e0e0e0; /* Light text color */
}

.dark-mode .footer-link {
  color: #94C8FF; /* Softer blue for dark mode */
}

.dark-mode .footer-link:hover {
  color: #99ccff; /* Lighter hover color */
}

.dark-mode .heart {
  color: #ff6666; /* Softer red for dark mode */
}